@media screen and (max-width: 1400px), screen and (max-device-width: 1400px) {
  body {
    font-size: 16px;
    line-height: 18px;
    .header {
      .container {
        .header__mainSearch {
          .search {
            margin-top: 40px;
          }
          .numbers {
            margin-top: 35px;
            ul {
              li {
                i {
                  width: 45px;
                  height: 45px;
                }
              }
            }
          }
          .workTime {
            margin: 40px 0 0 15px;
          }
        }
        .header__cabinet {
          margin-top: 40px;
          ul {
            li {
              &:nth-child(3) {
                a{
                  width: 290px;
                  i {
                    margin: 0 !important;
                  }
                }
              }
            }
          }
        }
        .header__nav-panel{
          &__goods{

          }
        }
      }
    }
    .content {
      .container {
        .content__actualProduct,
        .content__saleProduct {
          &__slider,
          &__slider {
            img {
              width: 150px;
              height: 330px;
            }
            .slider {
              width: 1010px;
              height: 330px;
              .lSSlideOuter {
                ul {
                  .item-a,
                  .item-b,
                  .item-c,
                  .item-e,
                  .item-d,
                  .item-f {
                    width: 235px;
                    height: 330px;
                  }
                }
              }
            }
          }
        }
        .content__howToMakeOrder {
          div {
            .info {
              margin-left: 55px;
            }
          }
        }
      }
    }
    .contentAddres {
      iframe {
        height: 465px;
      }
      .contentAddres__contactForm {
        .comentary {
          input[type*="text"] {
            width: 184px;
          }
          textarea {
            width: 395px;
          }
        }
        .contacts,
        .social{
          width: 75%;
        }
      }
    }
  }
}

@media screen and (max-width: 1050px), screen and (max-device-width: 1050px){
  body{
    font-size: 12px;
    line-height: 14px;
    .header{
      .container{
        .header__logo{
          a{
            margin-left: 0;
          }
        }
        .header__mainSearch {
          .search{
            input{
              height: 30px;
              line-height: 30px;
            }
            i{
              font-size: 1em;
            }
          }
          .numbers{
            ul{
              li{
                i{
                  width: 30px;
                  height: 30px;
                  font-size: 1.5em;
                }
              }
            }
          }
          .workTime{
            margin: 40px 0 0 10px;
            ul{
              margin-left: 10px;
              li{
                span{
                  font-size: 1em;
                  line-height: 1.125em;
                }
              }
            }
          }
        }
        .header__cabinet{
          ul{
            li{
              &:nth-child(3){
                a{
                  width: 215px;
                }
              }
            }
          }
        }
      }
    }
    .content{
      .container{
        .content__actualProduct,
        .content__saleProduct{
          &__headline,
          &__headline{
            div{
              p{
                font-size: 1em;
                line-height: 1.125em;
              }
            }
          }
          &__slider,
          &__slider{
            img{
              height: 270px;
            }
            .slider{
              width: 710px;
              height: 270px;
              .lSSlideOuter {
                ul {
                  .item-a,
                  .item-b,
                  .item-c,
                  .item-e,
                  .item-d,
                  .item-f {
                    width: 235px;
                    height: 270px;
                  }
                  li {
                    a{
                      img{
                        width: 150px;
                        height: 150px;
                      }
                      p{
                        font-size: 1.25em;
                        line-height: 1.375em;
                      }
                    }
                  }
                }
              }
            }
          }
        }
        .content__howToMakeOrder{
          div{
            height: 150px;
            .icons{
              .arrow{
                margin: 15px 0 0 15px;
              }
              .cart,
              .box,
              .truck{
                margin-top: 20px;
                width: 70px;
                height: 70px;
              }
            }
            .learnMore{
              a{
                width: 125px;
              }
            }
          }
        }
      }
    }
    .contentAddres {
      .contentAddres__contactForm {
        .comentary {
          input[type*="text"] {
            width: 144px;
          }
          textarea {
            width: 313px;
          }
        }
        .contacts,
        .social{
          width: 80%;
        }
      }
    }
  }
}